.home {
    text-align: center;
  }
  
  .home header {
    background-color: #ff5722;
    color: white;
    padding: 20px;
  }
  
  .home ul {
    list-style: none;
    padding: 0;
  }
  
  .home li {
    margin: 10px 0;
  }
  
  .home a {
    color: #ff5722;
    text-decoration: none;
  }
  
  .home a:hover {
    text-decoration: underline;
  }
  